.head-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 70px;
}
.head-image {
  width: auto;
  transition: max-width 1s, min-width 1s;
}
.head-image-expanded {
  min-width: 230px;
  max-width: 250px;
}
.head-image-contracted {
  min-width: 40px;
  max-width: 60px;
}
.head-text {
  font-family: "Hachi Maru Pop";
  margin: auto;
  color: #ffff;
  transition: font-size 1s;
}

.head-text-expanded {
  font-size: 48px;
}

.head-text-contracted {
  font-size: 12px;
}
